import Explain from "./pages/Explain";
import Home from "./pages/Home";
import Language from "./pages/Language";
import { HashRouter as Router, Switch, Route } from "react-router-dom";


function App() {
  return (
    <Router>
      <Switch>
        <Route exact path="/">
          <Home />
        </Route>
        <Route path="/Language">
          <Language />
        </Route>
        <Route path="/Explain">
          <Explain />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
